.form {
  border: 1px solid black;
  padding: 15px;
  margin-bottom: 20px;
  width: 100%;
}
.label {
  font-size: 22px;
}
.input {
  display: block;
  margin-top: 20px;
  margin-bottom: 20px;
  width: 90%;
  height: 30px;
  @media screen and (min-width: 1024px) {
    width: 250px;
  }
}
.button {
  display: block;
  width: 150px;
  height: 50px;
  font-size: 20px;
  border-radius: 4px;
  cursor: pointer;
}
